.crew {
  width: 100%;
  min-height: 100vh;
  background: url("../assets/crew/background-crew-desktop.jpg");
  background-size: 100% 100%;
  position: absolute;
  top: 0;
  overflow: hidden;
  h1 {
    text-transform: uppercase;
    font-family: "Barlow Condensed", sans-serif;
    letter-spacing: 2.7px;
    color: #ffffff;
    position: absolute;
    top: 20vh;
    left: 9rem;
    span {
      color: gray;
      padding-right: 1rem;
    }
  }
  .crew-container {
    left: 0;
    margin-top: 20vh;
    min-height: 80vh;
    color: #fff;
    width: 100%;
    display: flex;

    .crew-container-content {
      width: 50%;
      padding: 0 9rem;

      .crew-container-content-description {
        padding-top: 5rem;
        width: 90%;
        min-height: 70%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        h4 {
          font-size: 1.8rem;
          text-transform: uppercase;
          color: grey;
          font-weight: 400;
        }
        h3 {
          text-transform: uppercase;
          font-size: 3.2rem;
          font-weight: 400;
          margin: 1rem 0;
        }
        p {
          font-size: 1.5rem;
          color: rgb(208, 214, 249);
          padding: 1rem 0;
          line-height: 1.9rem;
          margin-bottom: 1rem;
        }
      }
      .buttons {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 20%;
        height: 1.5rem;
        margin: 4rem 0;
        button {
          height: 1rem;
          width: 1rem;
          border: none;
          border-radius: 50%;
          transition: background-color 0.3s ease;
          background-color: rgba(255, 255, 255, 0.3);
          &:hover {
            background-color: rgba(255, 255, 255, 0.6);
            cursor: pointer;
          }
        }
        .crew-active {
          background-color: rgb(255, 255, 255);
        }
      }
    }
    .crew-container-image {
      width: 50%;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      img {
        height: 45em;
        width: 35em;
      }
    }
  }
}

@media only screen and (max-width: 1390px) {
  .crew {
    .crew-container {
      .crew-container-content {
        width: 65%;
      }
      .crew-container-image {
        justify-content: flex-start;
        img {
          height: 35em;
          width: 25em;
        }
      }
    }
  }
}

@media only screen and (max-width: 1270px) {
  .crew {
    h1 {
      left: 2rem;
      top: 15vh;
      font-size: 1.5rem;
    }
    .crew-container {
      .crew-container-content {
        padding-left: 2rem;
        .crew-container-content-description {
          width: 100%;
        }
      }
    }
  }
}

@media only screen and (max-width: 1024px) {
  .crew {
    background: url("../assets/crew/background-crew-tablet.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    .crew-container {
      flex-direction: column;
      align-items: center;
      .crew-container-content {
        margin-top: 20vh;
        width: 65%;
        padding: 0;
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        .crew-container-content-description {
          padding-top: 0;
          align-items: center;
          h4 {
            font-size: 1.2em;
          }
          h3 {
            font-size: 2em;
          }
          p {
            font-size: 1.2em;
          }
        }
        .buttons {
          margin: 2rem;
          width: 10rem;
        }
      }
      .crew-container-image {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          height: 25em;
          width: 18em;
          position: relative;
          bottom: 0;
        }
      }
    }
  }
}

@media only screen and(max-width:556px) {
  .crew {
    background: url("../assets/crew/background-crew-mobile.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    h1 {
      font-size: 1rem;
    }
    .crew-container {
      .crew-container-content {
        width: 70%;
        .crew-container-content-description {
          h4 {
            font-size: 1em;
          }
          h3 {
            font-size: 1.5em;
          }
          p {
            font-size: 1em;
          }
        }
        .buttons {
          margin: 0.5rem;
        }
        .crew-container-image {
          img {
            height: 20em;
            width: 11em;
          }
        }
      }
    }
  }
}

@media only screen and(max-width:320px) {
  .crew {
    .crew-container {
      .crew-container-content {
        width: 80%;
      }
    }
  }
}
