.homepage {
  min-height: 100vh;
  width: 100%;
  background-image: url("../assets/home/background-home-desktop.jpg");
  background-size: 100% 100%;
  font-family: "Barlow Condensed", sans-serif;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  .content {
    min-height: 80vh;
    width: 85%;
    margin: 0 auto;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    position: relative;
    .content-description {
      width: 35rem;
      padding-top: 15rem;
      h1 {
        text-transform: uppercase;
        letter-spacing: 2.7px;
        font-weight: 150;
        font-size: 2.5em;
        opacity: 0.8;
        color: rgb(208, 210, 249);
        span {
          display: block;
          font-size: 4em;
          font-family: "Bellefair", serif;
          opacity: 1;
          color: #ffffff;
        }
      }
      p {
        font-size: 1.5rem;
        font-weight: 150;
        opacity: 0.8;
        color: rgb(208, 210, 249);
      }
    }
    .content-button-explore {
      background-color: rgba(255, 255, 255, 0.5);
      color: rgb(11, 13, 23);
      height: 20rem;
      width: 20rem;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      transition: transform 0.5s ease;
      &:hover {
        transform: scale(1.2);
        button {
          transform: scale(0.83);
        }
      }
      button {
        border: none;
        border-radius: 50%;
        height: 20rem;
        width: 20rem;
        font-size: 2rem;
        text-transform: uppercase;
        cursor: pointer;
        transition: transform 0.5s ease;
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .homepage {
    background-image: url("../assets/home/background-home-tablet.jpg");

    .content {
      align-items: center;
      justify-content: center;
      flex-direction: column;
      width: 100%;
      flex-basis: 100%;
      .content-description {
        margin: 0 auto;
        text-align: center;
        padding-top: 12rem;
      }
      .content-button-explore {
        margin: 1rem auto;
        height: 15rem;
        width: 15rem;
        button {
          height: 15rem;
          width: 15rem;
        }
      }
    }
  }
}

@media only screen and (max-width: 560px) {
  .homepage {
    .content {
      .content-description {
        width: 75%;
        h1 {
          font-size: 1.5em;
        }
        p {
          font-size: 1.2em;
        }
      }
      .content-button-explore {
        height: 12rem;
        width: 12rem;
        margin: 5rem 0;
        button {
          height: 12rem;
          width: 12rem;
        }
      }
    }
  }
}
@media only screen and (max-width: 400px) {
  .homepage {
    .content {
      .content-description {
        h1 {
          font-size: 1.2rem;
          span {
            font-size: 3rem;
          }
        }
      }
    }
  }
}
