.destinations {
  width: 100%;
  min-height: 100vh;
  background: url("../assets/destination/background-destination-desktop.jpg");
  background-size: 100% 100%;
  position: absolute;
  top: 0;
  left: 0;
  font-family: "Bellefair", serif;
  overflow: hidden;
  .destinations-container {
    color: rgb(208, 214, 249);
    height: 80%;
    width: 100%;
    margin-top: 20vh;
    h1 {
      margin: 2rem 0;
      padding-left: 9rem;
      text-transform: uppercase;
      font-family: "Barlow Condensed", sans-serif;
      letter-spacing: 2.7px;
      width: 100%;
      span {
        color: gray;
        padding-right: 1rem;
      }
    }
    .destinations-container-content {
      height: 80%;
      width: 90%;
      margin: 0 auto;

      .destinations-navbar {
        height: 10%;
        width: 100%;
        position: relative;
        font-family: "Barlow Condensed", sans-serif;

        ul {
          width: 32%;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: space-around;
          position: absolute;
          right: 20%;
          li {
            list-style-type: none;
            font-size: 1rem;
            text-transform: uppercase;
            letter-spacing: 2.7px;
            &:hover {
              cursor: pointer;
            }
          }
          .destinations-active {
            border-bottom: 2px solid #ffffff;
            padding: 0.625rem 0;
          }
        }
      }
      .destinations-content {
        width: 100%;
        height: 90%;
        display: flex;
        padding-top: 5rem;

        .destinations-content-image-container {
          height: 100%;
          width: 50%;
          display: flex;
          align-items: flex-start;
          justify-content: center;
          img {
            height: 30em;
            width: 30em;
          }
        }
        .destinations-content-description {
          width: 30%;
          padding: 5rem 3rem 5rem 0;
          h2 {
            font-size: 6.25em;
            text-transform: uppercase;
          }
          p {
            line-height: 1.5rem;
            color: rgb(208, 210, 249);
            padding: 2rem 0;
            opacity: 0.8;
            font-size: 1.2rem;
          }
          .destinations-line {
            width: 100%;
            height: 1px;
            opacity: 0.8;
            background-color: rgb(208, 210, 249);
          }
          .details {
            display: flex;
            padding: 1rem 0;
            text-transform: uppercase;
            .distance {
              padding-right: 5rem;
            }
            h5 {
              font-size: 0.825rem;
              opacity: 0.8;
              letter-spacing: 2.35px;
              font-family: "Barlow Condensed", sans-serif;
              padding: 0.8rem 0;
            }
            h4 {
              font-size: 1.75rem;
            }
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 1200px) {
  .destinations {
    .destinations-container {
      h1 {
        padding-left: 2rem;
        font-size: 1.5rem;
        text-align: center;
      }
      .destinations-container-content {
        .destinations-content {
          .destinations-content-image-container {
            img {
              height: 25em;
              width: 25em;
            }
          }
          .destinations-content-description {
            width: 50%;
            h2 {
              font-size: 5em;
            }
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 1024px) {
  .destinations {
    background: url("../assets/destination/background-destination-tablet.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    .destinations-container {
      margin-top: 17vh;
      h1 {
        padding-left: 0;
      }
      .destinations-container-content {
        .destinations-navbar {
          width: 100%;
          ul {
            width: 100%;
            margin: 0 auto;
            left: 0;
          }
        }
        .destinations-content {
          display: flex;
          flex-direction: column;
          align-items: center;
          .destinations-content-image-container {
            width: 100%;
            img {
              height: 15em;
              width: 15em;
            }
          }
          .destinations-content-description {
            width: 100%;
            padding: 0;
            text-align: center;
            .details {
              align-items: center;
              justify-content: center;
            }
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 480px) {
  .destinations {
    .destinations-container {
      h1 {
        font-size: 1rem;
      }
      .destinations-container-content {
        .destinations-content {
          .destinations-content-image-container {
            img {
              height: 12em;
              width: 12em;
            }
          }
          .destinations-content-description {
            h2 {
              font-size: 3.5em;
            }
          }
        }
      }
    }
  }
}
