.nav-bar {
  width: 100%;
  min-height: 5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  top: 2rem;
  padding-left: 2rem;
  letter-spacing: 2.7px;
  z-index: 9999;
  font-family: "Barlow Condensed", sans-serif;
  .line {
    width: 35%;
    height: 1px;
    background-color: rgb(208, 214, 249);
    position: relative;
    right: -3rem;
    z-index: 2;
  }
  .nav-links-display {
    transition: 0.5s ease;
  }
  .nav-links {
    width: 60%;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    display: flex;
    align-items: center;
    height: 6rem;
    ul {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-around;
      color: rgb(255, 255, 255);
      text-transform: uppercase;
      list-style-type: none;
      li {
        a {
          text-decoration: none;
          color: rgb(255, 255, 255);
          span {
            font-weight: bold;
            padding-right: 1rem;
          }
        }
        &:hover {
          cursor: pointer;
        }
      }
    }
  }
  .close-menu-icon,
  .open-navbar {
    background: transparent;
    border: none;
    cursor: pointer;
  }
  .toggle-button {
    display: none;
  }
}
.active {
  border-bottom: 3px solid #ffffff;
  padding: 2.2rem 0;
}

@media only screen and (max-width: 1300px) {
  .nav-links {
    ul {
      li {
        span {
          display: none;
        }
      }
    }
  }
  .line {
    display: none;
  }
}
@media only screen and (max-width: 560px) {
  .nav-bar {
    position: relative;
    top: 0;
    padding-top: 2rem;
    .open-navbar {
      display: block;
      padding: 0 2rem;
    }

    .nav-links {
      min-height: 115vh;
      .close-menu-icon {
        display: block;
        position: absolute;
        right: 2rem;
        top: 2rem;
      }
      position: fixed;
      top: 0;
      right: -24rem;
      width: 24rem;
      align-items: flex-start;
      flex-direction: column;
      display: none;

      ul {
        margin-top: 4rem;
        align-items: flex-start;
        flex-direction: column;
        li {
          margin: 2rem 0;
          padding-left: 1rem;
          width: 100%;
          span {
            display: inline-block;
          }
          font-size: 2em;
        }
      }
    }
    .nav-links-display {
      display: flex;
      transform: translateX(-24rem);
    }
  }
}

@media only screen and (max-width: 490px) {
  .nav-bar {
    .nav-links {
      width: 17rem;
      ul {
        li {
          font-size: 1.2em;
        }
      }
    }
  }
}
