.technology {
  width: 100%;
  min-height: 100vh;
  background: url("../assets/technology/background-technology-desktop.jpg");
  background-size: 100% 100%;
  position: absolute;
  top: 0;
  left: 0;
  color: rgb(255, 255, 255);
  h1 {
    text-transform: uppercase;
    font-family: "Barlow Condensed", sans-serif;
    letter-spacing: 2.7px;
    font-weight: 400;
    position: relative;
    top: 18vh;
    padding-left: 9rem;
    span {
      color: gray;
      padding-right: 1rem;
    }
  }
  .technology-container {
    width: 100%;
    min-height: 75vh;
    margin-top: 20vh;
    padding-left: 9rem;
    .image-container {
      width: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        height: 30em;
        width: 30em;
        border-radius: 4px;
      }
    }

    .technology-content-container {
      display: flex;
      height: 65vh;
      .technology-content {
        width: 50%;
        padding: 0 1rem;
        display: flex;
        align-items: center;
        justify-content: center;
        .technology-container-content-buttons {
          width: 30%;
          display: flex;
          align-items: center;
          justify-content: center;
          flex-direction: column;
          button {
            height: 5rem;
            width: 5rem;
            margin: 1rem 0;
            border-radius: 50%;
            background: transparent;
            border: 1px solid rgba(255, 255, 255, 0.6);
            color: #ffffff;
            font-size: 2rem;
            font-weight: 400;
            &:hover {
              cursor: pointer;
              border: 1px solid #ffffff;
            }
          }
          .technology-active {
            background-color: #ffffff;
            color: #0b0d17;
          }
        }
        .technology-container-content-text {
          width: 70%;
          display: flex;
          align-items: flex-start;
          justify-content: flex-start;
          flex-direction: column;
          h4 {
            font-size: 2rem;
            text-transform: uppercase;
            color: grey;
            font-weight: 400;
          }
          h3 {
            text-transform: uppercase;
            font-size: 3.5rem;
            font-weight: 400;
            margin: 1rem 0;
          }
          p {
            font-size: 1.5rem;
            color: rgb(208, 214, 249);
            padding: 1rem 0;
            line-height: 1.9rem;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 1300px) {
  .technology {
    h1 {
      padding-left: 2rem;
    }
    .technology-container {
      padding-left: 2rem;
    }
  }
}
@media only screen and (max-width: 1024px) {
  .technology {
    background: url("../assets/technology/background-technology-tablet.jpg");
    background-repeat: no-repeat;
    background-size: cover;

    .technology-container {
      margin-top: 30em;
      padding-left: 0;

      .image-container {
        width: 100%;
        img {
          height: 25em;
          width: 25em;
        }
      }
      .technology-content-container {
        flex-direction: column-reverse;
        .technology-content {
          width: 100%;
          flex-direction: column;
          .technology-container-content-buttons {
            flex-direction: row;
            width: 20em;
            justify-content: space-between;
          }
          .technology-container-content-text {
            align-items: center;
            text-align: center;
            width: 80%;
          }
        }
      }
    }
  }
}
@media only screen and (max-width: 736px) {
  .technology {
    h1 {
      font-size: 1.5rem;
    }
    .technology-container {
      margin-top: 26em;
      .technology-content-container {
        .technology-content {
          .technology-container-content-buttons {
            button {
              height: 3rem;
              width: 3rem;
            }
          }
          .technology-container-content-text {
            width: 90%;

            h4 {
              font-size: 1.5em;
            }
            h3 {
              font-size: 2rem;
            }
            p {
              font-size: 1.2em;
            }
          }
        }
      }
    }
  }
}
@media only screen and (max-width: 560px) {
  .technology {
    background: url("../assets/technology/background-technology-mobile.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    h1 {
      top: 12vh;
      text-align: center;
      padding-left: 0;
    }
    .technology-container {
      margin-top: 23em;
      .image-container {
        width: 100%;
        img {
          height: 22em;
          width: 22em;
        }
      }
      .technology-content-container {
        .technology-content {
          .technology-container-content-text {
            width: 100%;
          }
        }
      }
    }
  }
}
@media only screen and (max-width: 440px) {
  .technology {
    h1 {
      margin-top: 1rem;
      font-size: 1.2rem;
    }
    .technology-container {
      .technology-content-container {
        .technology-content {
          .technology-container-content-buttons {
            width: 15em;
            button {
              font-size: 1.5em;
            }
          }
          .technology-container-content-text {
            h3 {
              font-size: 1.5em;
            }
            h4 {
              font-size: 1em;
            }
            p {
              font-size: 1em;
            }
          }
        }
      }
    }
  }
}
@media only screen and (max-width: 375px) {
  .technology {
    h1 {
      font-size: 1em;
    }
    .technology-container {
      .image-container {
        img {
          height: 16em;
          width: 16em;
        }
      }
      .technology-content-container {
        .technology-content {
          .technology-container-content-buttons {
            width: 15em;
            button {
              font-size: 1.5em;
            }
          }
          .technology-container-content-text {
            width: 100%;
            h3 {
              font-size: 1.5em;
            }
            h4 {
              font-size: 1.2em;
            }
            p {
              font-size: 1em;
            }
          }
        }
      }
    }
  }
}
@media only screen and (max-width: 375px) and (min-height: 800px) {
  .technology {
    .technology-container {
      margin-top: 18em;
    }
  }
}
